@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@200;300;400;500;600;700;800;900&family=Roboto:ital,wght@1,100&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Outfit', sans-serif;
  
}
body{
  background-color: black;
}
h1,h4,p,a{
  color: #fff;
  text-decoration: none;
}
ul{
  list-style-type: none;
}

.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background-color: #0C2D48;
  color: black;
  border: 1px solid white;
  font-weight: 600;
  cursor: pointer;
}

.btn-light{
  background: transparent;
  color: #fff;
}
.btn:hover{
  background: rgb(255,255,255,0.2);
  color: #fff;
  transition: 0.3s;
}