.footer{
    width: 100%;
    padding: 1.4rem 0;
    background-color: rgba(19,19,19,0.8);
}
.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 40px;
}
.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.phone{
    display: flex;
    margin-bottom: 0.8rem;
}
.email{
    display: flex;
    margin-bottom: 0.8rem;
}
.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.socilaicon{
  margin-top: 1rem;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}
@media screen and (max-width:700px) {
    .footer-container{
      text-align: center;
        grid-template-columns: 1fr;
        
    }
    .footer{
        height: 100%;
        width: 100%;
        padding: 1rem 0;
    }
    .right{
        width: 100%;
    }
}